import styled from 'styled-components';
import { LayerPopup } from '../../components/common/LayerPopup';
import { priceFormat } from '../../utils/filter';

export const AsideClassPopup = ({ asideData, saleSelectedId }) => {
  return (
    <>
      <LayerPopup
        visible={false}
        title="   "
        selectedId={saleSelectedId}
      >
        <LayerSale>
          <LayerSaleH3> </LayerSaleH3>
          <LayerSaleDl>
            <LayerSaleDiv>
              <LayerSaleDt></LayerSaleDt>
              <LayerSaleDd>{priceFormat(asideData.price)}</LayerSaleDd>
            </LayerSaleDiv>
            <LayerSaleDiv>
              <LayerSaleDt>  </LayerSaleDt>
              <LayerSaleDd>
                -
                {priceFormat(
                  (asideData.price * (asideData.sale * 0.01)).toFixed(0),
                )}
                
              </LayerSaleDd>
            </LayerSaleDiv>
            <LayerSaleDiv>
              <LayerSaleDt>{asideData.monthlyInstallment} </LayerSaleDt>
              <LayerSaleDd>
                {asideData.sale}% 
                {priceFormat(
                  (
                    asideData.price / asideData.monthlyInstallment -
                    (asideData.price / asideData.monthlyInstallment) *
                      (asideData.sale * 0.01)
                  ).toFixed(0),
                )}
                
              </LayerSaleDd>
            </LayerSaleDiv>
          </LayerSaleDl>

          <LayerSaleH3>   </LayerSaleH3>
          <OrderList>
            <OrderItem>1. : 2, 3, 4, 5, 6, 7  </OrderItem>
            <OrderItem>2. : 2, 3, 4, 5, 6, 7  </OrderItem>
            <OrderItem>3. : 2, 3, 4, 5, 6  </OrderItem>
            <OrderItem>4. : 2, 3, 4, 5, 6, 7  </OrderItem>
            <OrderItem>5. : 2, 3, 4, 5, 6, 7  </OrderItem>
            <OrderItem>6. : 2, 3, 4  </OrderItem>
            <OrderItem>7. : 2, 3, 4, 5, 6, 7, 8  </OrderItem>
            <OrderItem>8. : 2, 3, 4, 5, 6, 7, 8  </OrderItem>
            <OrderItem>9. BC: 2, 3, 4, 5, 6, 7  </OrderItem>
            <OrderItem>10. : 2, 3, 4, 5  </OrderItem>
          </OrderList>

          <NoticeBox>
            <NoticeTitle as="p">[]</NoticeTitle>
            <Notice>
              <NoticeItem>
                         .
                      .
              </NoticeItem>
              <NoticeItem>
                 /()///GIFT//  
                 
              </NoticeItem>
              <NoticeItem>
                 /  , /  .
              </NoticeItem>
              <NoticeItem>
                 
                //,(),,(,),(B2B),,,,,
                 
              </NoticeItem>
              <NoticeItem>
                  PG  , ,    
                 
              </NoticeItem>
            </Notice>
          </NoticeBox>
        </LayerSale>
      </LayerPopup>
    </>
  );
};

const LayerSale = styled.div``;
const LayerSaleH3 = styled.h3`
  font-size: 18px;
  font-weight: bold;
  color: rgb(26, 26, 26);
  line-height: 24px;
  letter-spacing: -0.45px;
  margin: 0px;
`;
const LayerSaleDl = styled.dl`
  margin: 16px 0;
  padding: 16px 0;
  border-top: 1px solid rgb(248 248 248);
  border-bottom: 1px solid rgb(248 248 248);
`;
const LayerSaleDiv = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 8px;
`;
const LayerSaleDt = styled.dt`
  font-size: 14px;
  font-weight: normal;
  color: rgb(26, 26, 26);
  line-height: 20px;
  letter-spacing: -0.15px;
  margin: 0px;
`;
const LayerSaleDd = styled.dd`
  font-size: 14px;
  font-weight: normal;
  color: rgb(26, 26, 26);
  line-height: 20px;
  letter-spacing: -0.15px;
  margin: 0px;
`;
const OrderList = styled.ol`
  margin-top: 16px;
`;
const OrderItem = styled.li`
  font-size: 14px;
  font-weight: normal;
  color: rgb(26, 26, 26);
  line-height: 20px;
  letter-spacing: -0.15px;
  margin: 8px 0;
`;
const NoticeBox = styled.div`
  margin-top: 32px;
`;
const Notice = styled.ul``;
const NoticeItem = styled.li`
  font-size: 14px;
  font-weight: normal;
  color: #625f5f;
  line-height: 20px;
  letter-spacing: -0.15px;
  margin: 8px 0 0 18px;
  text-indent: -18px;
`;
const NoticeTitle = styled(NoticeItem)``;
